import React, { useState } from "react";
import { Link as RouterLink, useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import PropTypes from "prop-types";
import {useTranslation} from "react-i18next";
import { TRANSLATE_NS } from 'src/constants'
import { Controller, useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers';
import * as yup from 'yup';
import { useSnackbar } from 'notistack';
import { login } from "src/actions/accountActions";
import Logo from "src/components/Logo";
import LogoIcon from "src/components/LogoIcon";
import LogoText from "src/components/LogoText";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import { 
  Box,
  Button,
  CssBaseline, 
  Container, 
  FormControl, 
  Grid,
  Hidden, 
  makeStyles, 
  Paper,
  IconButton,
  InputLabel, 
  InputAdornment, 
  TextField, 
  Typography,
  OutlinedInput,
  withWidth, colors 
} from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
  errorWrapper: {
    backgroundColor: colors.red[100],
    padding: theme.spacing(2),
    margin: theme.spacing(2, 0)
  },
  paper: {
    marginTop: theme.spacing(16),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(4),
  },
  submit: {
    margin: theme.spacing(1, 0, 2),
    padding: theme.spacing(2),
  },
  forrgot: {
    marginTop: theme.spacing(2),
  },
  forrgotLink: {
    color: "#9e9e9e",
    paddingLeft: theme.spacing(0),
  },
  register: {
    color: theme.palette.info.main,
  },
  sectionDesktop: {
    display: "none",
    [theme.breakpoints.up("md")]: {
      display: "flex",
    },
  },
  sectionMobile: {
    display: "flex",
    [theme.breakpoints.up("md")]: {
      display: "none",
    },
  },
}));

const LoginForm = (props) => {
  const classes = useStyles()
  const dispatch = useDispatch()
  const user = useSelector(state=> state.account.user);
  const { enqueueSnackbar } = useSnackbar()
  const history = useHistory()
  const {t} = useTranslation([TRANSLATE_NS.TRANSLATIONS])  
  const schema = yup.object().shape({
    email: yup.string().email(t('translations:must be a valid email')).required(t('translations:required')),
    password: yup.string().required(t('translations:required')),
  })
  const { control, errors, handleSubmit, formState } = useForm({
    mode: "onChange",
    defaultValues: {
      email: 'chotmanee.t@arcadiaapm.com',
      password: 'mook'
    },    
    resolver: yupResolver(schema)
  })
  const { isSubmitting, isValid } = formState
  const [showPassword, setShowPassword] = useState(false)
  const [result, setResult] = useState(null)
  const handleClickShowPassword = () => {
    setShowPassword(prev=> !prev);
  }
  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  }
  const onSubmit = handleSubmit(async (data) => {
    try{

      console.log('isValid: ', isValid);

      if(!isValid){
        return;
      }

      const { email, password } = data;
      await dispatch(login(email, password))
      enqueueSnackbar(t('translations:login successfuly', {
        variant: 'success'
      }))
      history.push('/')
    }
    catch(error){
      console.log(error);
      setResult(()=> error);
    }
  })
  const hasError = inputName => !!(errors && errors[inputName])
  const errorMessage = inputName => errors[inputName]?.message ?? null

  console.log('user: ', user, isValid);

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Hidden smDown>
          <Logo />
        </Hidden>
        <Hidden only={["md", "lg", "xl"]}>
          <LogoIcon />
          <LogoText />
        </Hidden>
        <form
          className={classes.form}
          noValidate
          onSubmit={onSubmit}
        >
          {result && (
            <Paper 
              elevation={0}
              className={classes.errorWrapper}
            >
              <Typography variant="body2" color="error">
                {result?.data?.message}
              </Typography>
            </Paper>
          )}
          <Controller            
            name={"email"}
            control={control}
            render={(props) => (
              <TextField
                style={{marginBottom: 8}}
                variant="outlined"
                fullWidth
                label={t('translations:email')}
                value={props.value}
                {...props}
              />
            )}
          />
          {hasError("email") && (
            <div>
              <Typography variant="body2" color="error">
                {errorMessage("email")}
              </Typography>
            </div>
          )}

          <Controller
            name={"password"}            
            control={control}
            render={(props) => (
              <FormControl
                variant="outlined"
                margin="normal"
                fullWidth
                label={t('translations:password')}
                type="password"
                autoComplete="current-password"
                {...props}                
              >
                <InputLabel htmlFor="outlined-adornment-password">{t('translations:password')}</InputLabel>
                <OutlinedInput
                  id="outlined-adornment-password"
                  type={showPassword ? "text" : "password"}
                  value={props.value}                  
                  endAdornment={
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                        edge="end"
                      >
                        {showPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  }
                  labelWidth={70}
                />
              </FormControl>
            )}
          />
          {hasError("password") && (
            <div>
              <Typography variant="body2" color="error">
                {errorMessage("password")}
              </Typography>
            </div>
          )}
          <Grid className={classes.forrgot}>
            <Grid item xs>
              <Button
                component={RouterLink}
                to="/forgot-password"
                className={classes.forrgotLink}
              >
                {t('translations:forgot password')}
              </Button>
            </Grid>
          </Grid>
          <Button
            disabled={isSubmitting}
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            {t('translations:sign in')}
          </Button>
          <Grid container>
            <Grid item xs>
              <Box display="flex" justifyContent="center">
                <Typography variant="body2">
                  {t('translations:dont have an account')}
                  <Button
                    component={RouterLink}
                    to="/register"
                    className={classes.register}
                  >
                    {t('translations:create account')}
                  </Button>
                </Typography>
              </Box>
            </Grid>
          </Grid>
        </form>
      </div>      
    </Container>
  );
};

LoginForm.propTypes = {
  width: PropTypes.oneOf(["lg", "md", "sm", "xl", "xs"]).isRequired,
};

export default withWidth()(LoginForm);
